
import {mapMutations} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import {verifyPhone} from '../../utils/index.js'
export default {
name: '',
components: {
Group,
XInput,
XButton,
Toast,
Countup,
CheckIcon
},
data () {
return {
checkValue: true,
showToast: false,
toastText: '',
showToast1: false,
toastText1: '',
buttonStatus: false,
mobile: '',
password: '',
password1: '',
code: '',
counts: 60,
json: {
code: '',
mobile: '',
password: '',
source: 2,
username: ''
}
}
},
destroyed () { // 销毁时显示
this.components([true, true, true])
},
mounted () { // 隐藏
this.components([false, false, false])
},
methods: {
...mapMutations([
'components'
]),
async sendCode () {
let mobile = this.json.mobile
if (!mobile || !verifyPhone(mobile)) {
this.toast1('请填写正确手机号')
return false
}
let result = await this.axios.post('common/sendMessageAnd', {
mobile: this.json.mobile,
type: 1
})

if (result.success) {
this.toast('验证码已发送')
this.buttonStatus = true
} else {
this.toast1(result.message)
return false
}
let timer = setInterval(() => {
if (this.counts <= 0) {
this.buttonStatus = false
clearInterval(timer)
return
}
this.counts--
}, 1000)
console.log(1)
},
submit () {
let text = ''
let status = 0
if (!this.json.mobile.trim()) {
text = '请填写手机号'
} else if (!this.json.password.trim()) {
text = '请填写密码'
} else if (!this.password1.trim()) {
text = '请填写新密码'
} else if (this.json.password !== this.password1) {
text = '两次密码不一致'
} else if (!this.json.code.trim()) {
text = '请填写验证码'
} else {
status = 1
}
if (status) { // 修改成功时
this.set()
} else {
this.toast1(text)
}
},
async set () {
let result = await this.axios.post('member/register', this.json)

if (result.success) {
this.toast('账号已创建')
let timer = setTimeout(() => {
console.log('login')
this.$router.push({name: 'login'})
clearTimeout(timer)
}, 1000)
} else {
this.toast1(result.message)
return false
}
},
toast (mes) { // 成功弹窗
this.showToast = true
this.toastText = mes
},
toast1 (mes) { // 失败弹窗
this.showToast1 = true
this.toastText1 = mes
}
}
}



